.loader {
  border: 0 solid transparent;
  border-radius: 50%;
  width: 10rem;
  height: 10rem;
  position: relative;
  top: calc(50vh -5rem);
  left: calc(50vw -5rem); }

.loader::before, .loader::after {
  content: "";
  border: 1rem solid #C49B66;
  border-radius: 50%;
  width: inherit;
  height: inherit;
  position: absolute;
  top: 0;
  left: 0;
  animation: loader 2s linear infinite;
  opacity: 0; }

.loader::before {
  animation-delay: .5s; }

@keyframes loader {
  0% {
    transfrom: scale(0);
    opacity: 0; }
  50% {
    opacity: 1; }
  100% {
    transfrom: scale(1);
    opacity: 0; } }

